<template>
    <v-page :options="options">
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>添加卡片</h1>
            </v-header>
        </div>
        <div class="add-container">
            <div class="card-wrap">
                <div class="card-info" :class="'bg' + cardTypeNum[cardInfo.cardType]">
                    <div class="bank-name">
                        <div class="icon-wrap">
                            <i class="icon icon-dongyayinhang"></i>
                        </div>
                        <span class="bank">东亚银行</span>
                        <span class="card-type">（本地一本通）</span>
                    </div>
                    <div class="card-no font-num">6222 6688 8888 8888</div>
                    <p class="name">冯宝宝</p>
                    <div class="end-date">
                        <div class="left">
                            <span>VALID</span>
                            <span>FROM</span>
                        </div>
                        <div class="right">01/25</div>
                    </div>
                </div>
            </div>
            <div class="cell-wrap">
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">持卡人姓名</span>
                    <input type="tel"
                           placeholder="请输入持卡人姓名"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.name"
                    >
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">证件类型</span>
                    <input type="tel"
                           placeholder="请选择证件类型"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.type"
                           readonly
                    >
                    <div class="icon-wrap arrow" slot="right">
                        <i class="icon icon-arrow-bottom"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">证件号码</span>
                    <input type="text"
                           placeholder="请输入证件号码"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.certificateNum"
                    >
                    <div class="icon-wrap camera" slot="right">
                        <i class="icon icon-xiangji"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true" v-show="cardInfo.cardType === 'blueCard'">
                    <span class="title" slot="left">有效期</span>
                    <input type="tel"
                           placeholder="请选择证件类型"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.endDate"
                    >
                    <div class="icon-wrap wenhao" slot="right" @click="showEndDateExplain">
                        <i class="icon icon-wenhao"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true" v-if="cardInfo.cardType === 'redCard'">
                    <span class="title" slot="left">激活号码发送至</span>
                    <input type="tel"
                           placeholder="请选择手机号码"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.phone"
                           readonly
                    >
                    <div class="icon-wrap arrow" slot="right">
                        <i class="icon icon-arrow-bottom"></i>
                    </div>
                </v-cell-box>
                <v-cell-box :hasBorder="true">
                    <span class="title" slot="left">{{pwdTxtPlaceholder.pwdTxt}}</span>
                    <input type="password"
                           :placeholder="pwdTxtPlaceholder.pwdPlaceholder"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.pwd"
                    >
                </v-cell-box>
                <v-cell-box>
                    <span class="title" slot="left">短信验证码</span>
                    <input type="tel"
                           placeholder="请输入短信验证码"
                           class="input-box"
                           slot="middle"
                           v-model="cardInfo.checkNum"
                    >
                    <div class="check-num-btn" slot="right">
                        <button class="btn" :disabled="checkNumCutTime !== ''" @click="sendCheckNum">
                            <span>{{checkNumCutTime}}</span><span v-show="checkNumCutTime">s</span><span>{{checkNumTxt}}</span>
                        </button>
                    </div>
                </v-cell-box>
            </div>
            <div class="tips" v-show="showTips">
                <div class="symbol-wrap">
                    <v-symbol :name="'warn'"></v-symbol>
                </div>
                <p>短信验证码有误!</p>
            </div>
            <div class="btn-wrap">
                <v-linear-button :btnTxt="'添加'" :disabled="!canNext" @click="jumpResultPage"></v-linear-button>
            </div>
        </div>
        <v-popup-alert title="有效期说明" v-model="showAlert">
            <div class="explain-wrap">
                <div class="bg-wrap"></div>
                <p class="txt">有效期是打印在信用卡正面卡号下方，标准格式为月份在前，年份在后的一串数字</p>
            </div>
        </v-popup-alert>
        <v-toast v-model="showToast"
                 :toastTxt="toastTxt"
        ></v-toast>
    </v-page>
</template>

<script>
    import {vLinearButton} from '../../component/linearButton'
    import {cardTypeNum} from '../../dictionary'
    import {vCellBox} from '../../component/cell'
    import {vPopupAlert} from '../../component/popupAlert'
    export default {
        name: 'addBankCard',
        components: {
            vLinearButton,
            vCellBox,
            vPopupAlert
        },
        computed: {
            cardTypeNum () {
                return cardTypeNum
            },
            // 取款密码 或者 查询密码， blueCard - 查询密码， redCard - 查询密码， yellowCard - 取款密码
            pwdTxtPlaceholder () {
                return this.cardInfo.cardType === 'yellowCard' ?
                    {
                        pwdTxt: '取款密码',
                        pwdPlaceholder: '请再次输入登陆密码'
                    } :
                    {
                        pwdTxt: '查询密码',
                        pwdPlaceholder: '请输入查询密码'
                    }
            },
            // 能否点击下一步
            canNext () {
                return this.cardInfo.name !== '' &&
                    this.cardInfo.type !== '' &&
                    this.cardInfo.certificateNum !== '' &&
                    this.cardInfo.checkNum !== '' &&
                    this.cardInfo.pwd !== '' &&
                    ((this.cardInfo.cardType === 'blueCard' &&
                    this.cardInfo.endDate !== '') ||
                    (this.cardInfo.cardType === 'redCard' &&
                    this.cardInfo.phone !== '') ||
                    this.cardInfo.cardType === 'yellowCard')
            }
        },
        data () {
            return {
                options: {
                    scroll: false
                },
                cardInfo: {
                    name: '冯宝宝',
                    phone: '13527797920',           // 手机号
                    type: '身份证',      // 证件类型
                    certificateNum: '',  // 证件号码
                    endDate: '0822',     // 有效期
                    pwd: '',             // 密码
                    checkNum: '',        // 短信验证码
                    cardType: 'blueCard' // 卡类型，blueCard - 信用卡  redCard - 两地一本通 yellowCard - 借记卡
                },
                // 是是弹出框
                showAlert: false,
                // 是否显示提示信息
                showTips: false,
                // toast提示框信息
                toastTxt: '',
                // 是否显示toast提示框
                showToast: false,
                // 发送验证码按钮文本
                checkNumTxt: '获取验证码',
                // 发送倒计时
                checkNumCutTime: '',
                // 验证码定时器对象
                checkNumTimer: null,
            }
        },
        methods: {
            // 显示有效期说明
            showEndDateExplain () {
                this.showAlert = true
            },
            // 模拟卡片类型
            randomCardType () {
                let randNum = Math.random()
                this.cardInfo.cardType = randNum < 0.33 ? 'blueCard' : randNum < 0.66 ? 'redCard' : 'yellowCard'
            },
            // 跳转到绑卡结果页
            jumpResultPage () {
                let jumpUrl = '/login/addBankCard/addBankCardResult'
                window.T.go(jumpUrl)
            },
            // 发送短信验证码
            sendCheckNum () {
                // 校验手机号
                let isPhoneNum = window.T.Validator.phone(this.cardInfo.phone, '手机号')
                if (isPhoneNum !== true) {
                    this.toastTxt = isPhoneNum
                    this.showToast = true
                    return
                }
                this._checkNumCutTime('checkNumTxt','checkNumCutTime', 'checkNumTimer')
            },
            // 短信验证码倒计时
            _checkNumCutTime (btnTxtKey, timeKey, timerKey) {
                this[timeKey] = '60'
                let oldBtnTxt = this[btnTxtKey]
                this[btnTxtKey] = '重新发送'
                this[timerKey] = setInterval(() => {
                    this[timeKey] --
                    if (this[timeKey] < 1) {
                        this[timeKey] = ''
                        this[btnTxtKey] = oldBtnTxt
                        clearInterval(timer)
                    }
                }, 1000)
            }
        },
        created () {
            this.randomCardType()
        }
    }
</script>

<style lang="scss" scoped>
    .add-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: absolute;
        min-height: px2rem(1200);
        border-top: 1px solid #F0F0F0;
        font-family: PingFangSC-Regular;
        padding: px2rem(46) 0;
        .card-wrap {
            display: flex;
            justify-content: center;
            margin-bottom: px2rem(58);
            flex: none;
            .card-info {
                width: px2rem(628);
                padding: px2rem(40) px2rem(38) px2rem(68) px2rem(38);
                &.bg0 {
                    background: url("./images/card-blue.png") no-repeat center;
                    background-size: 100%;
                    color: #4B5E83;
                }
                &.bg1 {
                    background: url("./images/card-red.png") no-repeat center;
                    background-size: 100%;
                    color: #8B3838;
                }
                &.bg2 {
                    background: url("./images/card-yellow.png") no-repeat center;
                    background-size: 100%;
                    color: #845814;
                }
                .bank-name {
                    display: flex;
                    align-items: center;
                    margin-bottom: px2rem(54);
                    .icon {
                        width: px2rem(24);
                        height: px2rem(36);
                        font-size: px2rem(50);
                        line-height: px2rem(50);
                        font-weight: bold;
                    }
                    .bank {
                        font-size: px2rem(28);
                        font-weight: bold;
                        margin-right: px2rem(12);
                    }
                    .card-type {
                        font-size: px2rem(28);
                        line-height: px2rem(40);
                    }
                }
                .card-no {
                    margin-left: px2rem(8);
                    font-size: px2rem(44);
                    line-height: px2rem(52);
                    margin-bottom: px2rem(2);
                    height: px2rem(58);
                }
                .name {
                    font-size: px2rem(28);
                    height: px2rem(58);
                    line-height: px2rem(58);
                    font-family: PingFangSC-Medium;
                    margin-bottom: px2rem(12);
                }
                .end-date {
                    display: flex;
                    align-items: center;
                    .left {
                        display: flex;
                        flex-direction: column;
                        height: px2rem(40);
                        width: px2rem(73);
                        font-family: PingFangSC-Medium;
                        font-size: px2rem(18);
                        line-height: px2rem(20);
                    }
                    .right {
                        font-family: PingFangSC-Semibold;
                        height: px2rem(42);
                        line-height: px2rem(42);
                        font-size: px2rem(30);
                    }
                }
            }
        }
        .cell-wrap {
            font-size: px2rem(32);
            flex: none;
            span {
                display: block;
            }
            .title {
                width: px2rem(230);
                line-height: px2rem(44);
            }
            .info {
                line-height: px2rem(36);
            }
            input {
                width: 100%;
                font-size: px2rem(32);
            }
            .check-num-btn {
                color: #F28300;
                font-family: PingFangSC-Medium;
                margin-left: px2rem(10);
                font-size: px2rem(28);
                line-height: px2rem(40);
                span{
                    display: inline;
                }
                .btn {
                    background-color: #ffffff;
                    border: none;
                    height: 1rem;
                    padding: 0 .2rem;
                    color: #F28300;
                    @include font-color(#F28300);
                    &[disabled]{
                        color: #dcbf9d;
                    }
                }
            }
            .icon-wrap {
                width: px2rem(48);
                height: px2rem(48);
                display: flex;
                align-items: center;
                justify-content: flex-end;
                color: #9B9B9B;
                .icon {
                    width: px2rem(48);
                    height: px2rem(48);
                }
                .icon-xiangji {
                    font-size: px2rem(40);
                    line-height: px2rem(48);
                }
                .icon-wenhao {
                    font-size: px2rem(48);
                }
                &.arrow {
                    .icon {
                        width: px2rem(24);
                        height: px2rem(24);
                    }
                }
            }
        }
        .tips {
            display: flex;
            flex: none;
            align-items: center;
            height: px2rem(60);
            padding: 0 px2rem(20);
            color: #FF3F3F;
            font-size: px2rem(24);
            line-height: px2rem(40);
            width: 100%;
            box-sizing: border-box;
            .symbol-wrap {
                margin-right: px2rem(12);
                display: flex;
                justify-content: center;
                align-items: center;
                .ui-symbol {
                    height: px2rem(24);
                    width: px2rem(24);
                }
            }
            p {
                height: .4rem;
                line-height: .4rem;
            }
        }
        .btn-wrap {
            width: px2rem(670);
            height: px2rem(90);
            margin-top: px2rem(64);
            flex: none;
        }
    }

    .explain-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        .bg-wrap {
            background: url("./images/pic-analyz.png") no-repeat center;
            background-size: 100%;
            width: px2rem(348);
            height: px2rem(214);
            margin-bottom: px2rem(20);
        }
        .txt {
            color: #9B9B9B;
            font-family: PingFangSC-Regular;
            font-size: px2rem(24);
            line-height: px2rem(34);
            word-break: break-all;
        }
    }
</style>



